//layout mixins - start

@mixin qodefTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin qodefTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin qodefTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//typography mixins - start

@mixin qodefHeaderStyles($size: 60px) {
	font-family: $default-heading-font;
	font-size: $size;
	line-height: 1.3em;
    font-weight: 700;
    letter-spacing: -.025em;
    color: $default-heading-color;
}

//typography mixins - end

//hover mixins - start

@mixin qodefMainHoverStyles() {
	position: relative;
    color: $default-heading-color;

    &:before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        left: -4px;
        width: 0;
        height: 4px;
        background-color: $first-main-color;
        z-index: -1;
        @include qodefTransition(width .3s ease);
    }

    &:hover {

        &:before {
            width: calc(100% + 8px);
        }
    }
}

//hover mixins - end

//transition mixins - start

@mixin qodefTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin qodefTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin qodefTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin qodefAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin qodefTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.qodef-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .qodef-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .qodef-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include qodefTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .qodef-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            @include qodefTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include qodefTransition(opacity .2s ease-in-out);
    }
}

@mixin qodefImageOverlayHoverCustomStyle($with-hover: true) {

    @if ($with-hover) {
        &:after {
			content: '\26';
			position: absolute;
			top: auto;
			bottom: 0;
			left: 0;
			width: 77px;
			height: 77px;
			font-family: 'ElegantIcons';
			font-size: 20px;
			line-height: 77px;
			text-align: center;
			color: rgba($default-heading-color, .9);
            background-color: $additional-color;
            @include qodefTransform(translate3d(-100%, 0, 0));
            @include qodefTransitionTransform(.4s cubic-bezier(0.23, 1, 0.32, 1));
            will-change: transform;
        }
        
        &:hover {
            &:after {
                @include qodefTransform(translate3d(0, 0, 0));
            }
        }

    } @else {
        content: '\26';
		position: absolute;
		top: auto;
		bottom: 0;
		left: 0;
		width: 77px;
		height: 77px;
		font-family: 'ElegantIcons';
		font-size: 20px;
		line-height: 77px;
		text-align: center;
		color: rgba($default-heading-color, .9);
		background-color: $additional-color;
        @include qodefTransform(translate3d(-100%, 0, 0));
        @include qodefTransitionTransform(.4s cubic-bezier(0.23, 1, 0.32, 1));
        will-change: transform;
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
	@include qodefRelativeHolderLayout(top);
	margin: 40px 0 0;
	clear: both;
	
	ul {
		@include qodefRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 5px 0 0;
			
			.page-numbers {
				position: relative;
				display: inline-block;
				vertical-align: top;
				width: 48px;
				height: 48px;
				line-height: 48px;
				font-size: 16px;
				font-weight: 700;
				color: #fff;
				background-color: $default-heading-color;
				@include qodefTransition(color .2s ease-out, background-color .2s ease-out);
				
				&.current,
				&:hover {
					color: $default-heading-color;
					background-color: $additional-color;
				}
			}
			
			&.qodef-pag-number {
				
				a {
					display: inline-block;
					width: 48px;
					height: 48px;
					line-height: 48px;
					font-weight: 700;
					color: #fff;
					background-color: $default-heading-color;
					@include qodefTransition(color .2s ease-out, background-color .2s ease-out);
				}
				
				&:hover {
					
					a {
						color: $default-heading-color;
						background-color: $additional-color;
					}
				}
			}
			
			&.qodef-pag-active {
				
				a {
					color: $default-heading-color;
					background-color: $additional-color;
				}
			}
			
			&.qodef-pag-prev,
			&.qodef-pag-next {
				margin: 0 5px 0 0;
				
				a {
					display: inline-block;
					
					span {
						display: block;
						width: 48px;
						height: 48px;
						line-height: 48px;
						font-size: 24px;
						font-weight: 700;
						color: #fff;
						background-color: $default-heading-color;
						@include qodefTransition(color .2s ease-out, background-color .2s ease-out);
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
					
					&:hover {
						
						span {
							color: $default-heading-color;
							background-color: $additional-color;
						}
					}
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.qodef-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.qodef-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
					color: $first-main-color;
				}
				
				a {
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;
						
						&:before {
							display: block;
							font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
							font-size: 24px;
							line-height: 48px;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
					
					&.prev {
						
						&:before {
							content: "\34";
						}
					}
					
					&.next {
						
						&:before {
							content: "\35";
						}
					}
				}
			}
		}
	}
}

@mixin qodefButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 13px;
    line-height: 2em;
    letter-spacing: 0.05em;
    font-weight: 700;
    text-transform: uppercase;
	border-radius: 5em;
    outline: none;
    box-sizing: border-box;
    @include qodefTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 14px 30px;
	    font-size: 10px;
        
    } @else if ($size == 'medium') {
        padding: 11px 33px;
        
    } @else if ($size == 'large') {
        padding: 16px 36px;
        
    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 16px 26px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: $default-heading-color $important;
    background-color: $additional-color $important;
    border: 1px solid $additional-color $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $additional-color $important;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $additional-color $important;
    border-color: $additional-color $important;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

@mixin qodefBackgroundGradient {
	background: #000000;
	background: -moz-linear-gradient(left, #000000 0%, #181818 50%, #000000 100%);
	background: -webkit-linear-gradient(left, #000000 0%,#181818 50%,#000000 100%);
	background: linear-gradient(to right, #000000 0%,#181818 50%,#000000 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000',GradientType=1 );
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 15px;
    padding: 16px 18px;
    font-family: inherit;
    font-size: 13px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid #e1e1e1;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: pointer;
    box-sizing: border-box;
    @include qodefTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

